import brandList from '../../components/brandList/brandList.vue'
import newgoods from '../../components/brandList/newgoods.vue'

import MPLocation from '../../components/indexs/MPLocation.vue'







export default {
	name: "",
	components: {

		MPLocation,







		brandList,
		newgoods
	},
	props: {},
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			banners: [],
			channel: [],
			brandList: [],
			newgoods: [],
		}
	},
	methods: {
		channels(index) {
			// console.log(index)
			let indexs = index
			uni.navigateTo({
				url: `../category/category?indexs=${indexs}`
			})
		},
	
	},
	onNavigationBarButtonTap(e) {
		console.log(11111)
	},
	mounted() {
		
		// console.log(this.$store.state)
		// 获取顶部轮播图
		uni.request({
			url: this.$api('index/index'),
			method: 'GET',
			data: {},
			success: res => {
				console.log(res)
				this.banners = res.data.banner
				this.channel = res.data.channel
				this.brandList = res.data.brandList
				this.newgoods = res.data.newGoods
				console.log(this.newgoods)
			},
			fail: (err) => {
				console.log(err)
			},
			complete: () => {}
		});
		// 获取定位

		
 
	},
	onshow(){
	
